/**
 * window.localStorage 
 * @method set 
 * @method get 
 * @method remove 
 * @method clear 
 */
export const Local = {
    // 
    set(key, val) {
        window.localStorage.setItem(key, JSON.stringify(val));
    },
    // 
    get(key) {
        let json = window.localStorage.getItem(key);
        return JSON.parse(json);
    },
    // 
    remove(key) {
        window.localStorage.removeItem(key);
    },
    // 
    clear() {
        window.localStorage.clear();
    },
};

